  font-size: 1.1vmax;
  margin-bottom: 0px !important;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-transform: capitalize;
  color: rgb(85, 62, 88);
  font-weight:normal;
  letter-spacing: 2px;
}
.readquote{
  margin-left: -2px;
  border:transparent;
  color:rgb(29, 0, 29);
  overflow: hidden;
  outline: none;
  resize: none;
  height: auto;
  width: auto;
  padding: 5px 10px;
  padding-left: 2px;
  word-spacing: 1px;
  line-height: 160% !important;
  color: rgb(11, 1, 49);
  font-size: 1.2vmax;
}
.blockquotes
{
  border-top: 2px solid rgb(255, 193, 255);
  border-bottom: 2px solid rgb(255, 193, 255);
  border-left: 8px solid rgb(56, 0, 56);
  box-sizing: border-box;
  padding: 10px 50px;
  margin-top: 20px;
  background-color: #fdcffdd0;
  width:28vmax;
}
.storyread {
    grid-row-gap: 2px;
    row-gap: 2px;
    outline: none;
    resize: none;
    height: auto;
    width: 70%;
    border: none;
    background-color: transparent;
    padding: 5px 0px;
    word-spacing: 1px;
    line-height: 180% !important;
    color:rgb(11, 1, 49);
    font-size: 1.2vmax;
  }

  ::-webkit-scrollbar{
    width: 0;
  }
  #scrollpath{
    position: fixed;
    top:0;
    right:0;
    width:10px;
    height: 100%;
    background-color: (255,255,255,0.05);
  }
  #progressbar{
    position: fixed;
    top:0;
    right:0;
    width:10px;
    height: 100%;
    background: linear-gradient(to top,#008aff,#00ffe7);
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
  }

  @-webkit-keyframes animate {
    0%,100%{
      -webkit-filter:hue-rotate(0deg);
              filter:hue-rotate(0deg);
    }
    50%{
      -webkit-filter:hue-rotate(180deg);
              filter:hue-rotate(180deg)
    }
  }

  @keyframes animate {
    0%,100%{
      -webkit-filter:hue-rotate(0deg);
              filter:hue-rotate(0deg);
    }
    50%{
      -webkit-filter:hue-rotate(180deg);
              filter:hue-rotate(180deg)
    }
  }

  #progressbar:before{
    content:'';
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height: 100%;
    background: linear-gradient(to top,#008aff,#00ffe7);
    -webkit-filter:blur(10px);
            filter:blur(10px)
  }
  #progressbar:after{
    content:'';
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height: 100%;
    background: linear-gradient(to top,#008aff,#00ffe7);
    -webkit-filter:blur(30px);
            filter:blur(30px)
  }
